import { DetailpageComponent } from './detailPages/detailpage/detailpage.component';
import { BodyComponent } from './body/body.component';

import { Routes, RouterModule, PreloadingStrategy } from '@angular/router';
import { NgModule, Component } from '@angular/core';
import { CommonModule } from '@angular/common';


const routes: Routes = [
  {path: '', redirectTo: '/Trang-Chu', pathMatch: 'full'},
  {path: 'Trang-Chu', component: BodyComponent},
  {path: 'Gioi-Thieu', component: GioiThieuComponent},
  {path: 'Chuc-Nang-Nghiep-Vu', component: ChucNangNghiepVuComponent},
  {path: 'Co-Cau-To-Chuc', component: CoCauToChucComponent},
  {path: 'Tin-Tuc', component: ListTinTucComponent},
  {path: 'Cau-Hoi-Thuong-Gap', component: HoiDapComponent},
  {path: 'Gui-Cau-Hoi-Yeu-Cau', component: YeuCauComponent},
  {path: 'Lien-He', component: LienHeComponent},

  {path: 'Tin-Tuc/:DanhMuc/:TheLoaiTinID', component: MorePageComponent},
  {path: 'detail/:TinTucID', component: DetailpageComponent},

  {path: 'dauTuPhatTrien', component: ThongTinDuAnComponent},
  // {path: 'moreNewsList/:TheLoaiTinID', component: MorePageComponent},

  {path: 'MediaList', component: MorePageMediaComponent},
  {path: 'mediaDetail/:MediaID', component: DetailMediaComponent},



  {path: '404', component: NotfoundComponent},
  {path: '**', redirectTo: '/404'}

];



@NgModule({
  declarations: [],
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [BodyComponent, DetailpageComponent, ThongTinDuAnComponent, MorePageComponent, MorePageMediaComponent,
  DetailMediaComponent, GioiThieuComponent, HoiDapComponent, LienHeComponent, ListTinTucComponent, ChucNangNghiepVuComponent,
  CoCauToChucComponent, YeuCauComponent, NotfoundComponent];
