import { Router, ActivatedRoute } from '@angular/router';
import { IMediaList } from 'src/services/medialist';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from './../../../services/call-api.service';
import { Component, OnInit } from '@angular/core';
declare const carousel: any;
import { PageState } from '../../paginate/paginate.model';

@Component({
  selector: 'app-more-page-media',
  templateUrl: './more-page-media.component.html',
  styleUrls: ['./more-page-media.component.css']
})
export class MorePageMediaComponent implements OnInit {
public listMedia = [];
page: PageState = {
  currentPage : 1,
  pageSize : 5,
};

currentChange;

  constructor(private callApiService: CallApiService, private http: HttpClient, private router: Router,
              private activRouter: ActivatedRoute) {
  }


  ngOnInit() {
    this.loadData(this.page.currentPage);

}

loadData(pageNum) {
  const url = 'Media/GetList_ByType?pageNumber=' + pageNum + '&rowsPerPage=' + this.page.pageSize + '&TheLoaiTinID=21&PortalID=0';
  this.callApiService.getMediaList(url)
  .subscribe((data: IMediaList[]) => {
    this.listMedia = data;
    this.page.totalItems = data[0].TotalCount;
    window.scroll(0, 0);
  });
}

pageChange(pageState: PageState, isPageSizeChange = false) {
  // alert(pageState.currentPage);
  this.loadData(pageState.currentPage);
}
}
