import { Component, OnInit } from '@angular/core';
declare const DecreaseContrast: any;
declare const IncreaseContrast: any;
declare const tangFormChu: any;
declare const giamFormChu: any;
declare const PrintElem: any;

@Component({
  selector: 'app-co-cau-to-chuc',
  templateUrl: './co-cau-to-chuc.component.html',
  styleUrls: ['./co-cau-to-chuc.component.css']
})
export class CoCauToChucComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  tangPhanQuang() {
    DecreaseContrast();
  }
  giamPhanQuang() {
    IncreaseContrast();
  }

  tangSize() {
    tangFormChu();
  }

  giamSize() {
    giamFormChu();
  }

  inBai() {
    PrintElem('#printContent');
  }
}
