
import { IMedia } from 'src/services/media';
import { CallApiService } from 'src/services/call-api.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
declare const carousel: any;
declare const SlideImagecarousel: any;

@Component({
  selector: 'app-media',
  templateUrl: './media.component.html',
  styleUrls: ['./media.component.css']
})
export class MediaComponent implements OnInit {
  public mediass;
  public videoHD;
  url = 'Media/GetListImage?TheLoaiTinID=21&SoLuong=3&PortalID=0';
  urlVideo = 'Media/GetListImage?TheLoaiTinID=22&SoLuong=5&PortalID=0';

  constructor(private callApiService: CallApiService, private activeRouter: ActivatedRoute) { }
  ngOnInit() {


    // this.callApiService.getMedia(this.url)
    // .subscribe((data: IMedia[]) => {
    //   this.media = data;
    //   console.log(data);
    // });
    const empid = this.activeRouter.snapshot.params.MediaID;
    this.callApiService.getMedia(this.url + empid)
    .subscribe((data: IMedia[]) => {
      // SlideImagecarousel(data);
      this.mediass = data;
    });
    // carousel();

    const empidd = this.activeRouter.snapshot.params.MediaID;
    this.callApiService.getMedia(this.urlVideo   + empidd)
    .subscribe((data: IMedia[]) => {
      this.mediass = data;

    });
    // carousel();
  }

}
