import { Component, OnInit } from '@angular/core';
declare const DecreaseContrast: any;
declare const IncreaseContrast: any;
declare const tangFormChu: any;
declare const giamFormChu: any;
declare const PrintElem: any;

@Component({
  selector: 'app-gioi-thieu',
  templateUrl: './gioi-thieu.component.html',
  styleUrls: ['./gioi-thieu.component.css']
})
export class GioiThieuComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  tangPhanQuang() {
    DecreaseContrast();
  }
  giamPhanQuang() {
    IncreaseContrast();
  }

  tangSize() {
    tangFormChu();
  }

  giamSize() {
    giamFormChu();
  }

  inBai() {
    PrintElem('#printContent');
  }

}
