import { IDauTu } from 'src/services/duandautu';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from 'src/services/call-api.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
declare const goUp: any;

@Component({
  selector: 'app-kehoach',
  templateUrl: './kehoach.component.html',
  styleUrls: ['./kehoach.component.css']
})
export class KehoachComponent implements OnInit {
 public dautuList;
  url = 'DuAnDauTu/GetListLoaiDuAn?ParentID=18';
  constructor(private callApiService: CallApiService, private http: HttpClient, private activeRouter: ActivatedRoute,
              private router: Router) {

    this.callApiService.getDauTu(this.url)
        .subscribe((data: IDauTu[]) => {
          this.dautuList = data;
          window.scroll(0, 0);
        });
   }

  ngOnInit() {
  }
  onPageDetailS(ID: number) {
    localStorage.removeItem('ID');
    localStorage.setItem('ID', ID.toString());
    this.router.navigate(['dauTuPhatTrien']);
    window.scroll(0, 0);
  }
  goToTop() {
    goUp();
  }

}
