import { IDichVu } from './../../../services/dichvu';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from 'src/services/call-api.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dich-vu',
  templateUrl: './dich-vu.component.html',
  styleUrls: ['./dich-vu.component.css']
})
export class DichVuComponent implements OnInit {
  public dichvu = [];
  url = 'LienKet/GetListBanner?PortalID=0';
  constructor(private callApiService: CallApiService, private http: HttpClient, private router: Router) { }

  ngOnInit(): void {
    this.callApiService.getDichVu(this.url)
    .subscribe((data: IDichVu[]) => {
      this.dichvu = data;
      window.scroll(0, 0);
    });
  }

}
