
import { IDichVu } from 'src/services/dichvu';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from 'src/services/call-api.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-promotion01',
  templateUrl: './promotion01.component.html',
  styleUrls: ['./promotion01.component.css']
})
export class Promotion01Component implements OnInit {
  public promo = [];
  url = 'LienKet/GetListTieuDiem?PortalID=0';
  constructor(private callApiService: CallApiService, private http: HttpClient, private router: Router,
              private activeRouter: ActivatedRoute) { }

  ngOnInit(): void {
    this.callApiService.getDichVu(this.url)
    .subscribe((data: IDichVu[]) => {
      // tslint:disable-next-line: prefer-const
      let dichvu1: IDichVu[] = [];
      // tslint:disable-next-line: only-arrow-functions
      data.forEach(function(item) {
        if (item.ViTri === 1) {
          dichvu1.push(item);
        }
         });
      this.promo = dichvu1;

    });
  }
}
