import { Observable, of } from 'rxjs';
import { IDetailList } from 'src/services/detailList';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from 'src/services/call-api.service';
import { ChangeDetectionStrategy, Component, OnInit, Input } from '@angular/core';
import { delay, map, tap } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
declare const phanTrang: any;

@Component({
  selector: 'app-list-tin-tuc',
  templateUrl: './list-tin-tuc.component.html',
  styleUrls: ['./list-tin-tuc.component.css']
})
export class ListTinTucComponent implements OnInit {

  public tinTucList = [];

  public urlGTSGT = 'Gioi-Thieu-So-Giao-Thong-Van-Tai/' + btoa('9') + '.html' ;
  public urlCDVTT = 'Cac-Don-Vi-Truc-Thuoc/' + btoa('10') + '.html' ;
  public urlVTHK = 'Van-Tai-Hanh-Khach-Cong-Cong/' + btoa('11') + '.html' ;
  public urlCCHC = 'Cai-Cach-Hanh-Chinh/' + btoa('12') + '.html' ;

  public urlCLDHKH = 'Chien-Luoc-Dinh-Huong-Ke-Hoach/' + btoa('13') + '.html' ;
  public urlTTCDDH = 'Thong-Tin-Chi-Dao-Dieu-Hanh/' + btoa('14') + '.html' ;
  public urlGTDB = 'Giao-Thong-Duong-Bo/' + btoa('30') + '.html' ;
  public urlGTDT = 'Giao-Thong-Duong-Thuy/' + btoa('31') + '.html' ;

  url = 'TinTuc/GetList_ByTheLoai_Paging?pageNumber=1&rowsPerPage=10&TheLoaiTinID=';
  constructor(private callApiService: CallApiService, private http: HttpClient, private activeRouter: ActivatedRoute) {
    const empid = this.activeRouter.snapshot.params.TheLoaiTinID;
    this.callApiService.getDetailList(this.url + empid)
    .subscribe((data: IDetailList[]) => {
      this.tinTucList = data;
      window.scroll(0, 0);
    });
  }


  ngOnInit() {

}


}

