import { ITinLienQuan } from 'src/services/tinlienquan';
import { IMediaDetail } from './../../../services/detailmedia';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { CallApiService } from 'src/services/call-api.service';
import { Component, OnInit, Renderer2 } from '@angular/core';
declare const DecreaseContrast: any;
declare const IncreaseContrast: any;
declare const tangFormChu: any;
declare const giamFormChu: any;
declare const PrintElem: any;
declare const addLinked: any;

@Component({
  selector: 'app-detail-media',
  templateUrl: './detail-media.component.html',
  styleUrls: ['./detail-media.component.css']
})
export class DetailMediaComponent implements OnInit {
public detailMedia;
public tinLienQuan;
  url = 'Media/GetMediaDetails?MediaID=';
  url2 = 'TinTuc/TinLienQuan?TinTucID=1&SoLuong=5';
  constructor(private callApiService: CallApiService, private http: HttpClient, private router: Router, private render: Renderer2,
              private activeRouter: ActivatedRoute) { }

  ngOnInit() {
    const empid = this.activeRouter.snapshot.params.MediaID;
    this.callApiService.getMediaDetail(this.url + empid)
    .subscribe((data: IMediaDetail[]) => {
      this.detailMedia = data;
      window.scroll(0, 0);
    });

    this.callApiService.getTinLienQuan(this.url2)
    .subscribe((data: ITinLienQuan[]) => {
      //   // tslint:disable-next-line: only-arrow-functions
      // data.forEach(function(item) {
      // const thuGonTieuDe = ((item.TieuDe.length >= 65) ? item.TieuDe.substring(0, 65) + '...' : item.TieuDe.substring(0));
      // item.TieuDe = thuGonTieuDe ;
      // this.tinLienQuan.TieuDe = data[data.length - 1];
      this.tinLienQuan = data;
    });
  // });
  }
  tangPhanQuang() {
    DecreaseContrast();
  }
  giamPhanQuang() {
    IncreaseContrast();
  }

  tangSize() {
    tangFormChu();
  }

  giamSize() {
    giamFormChu();
  }

  inBai() {
    PrintElem('#printContent');
  }
  }
